<template>
	<view class="container">
		<view class="bar">
			<view @click="playmusic">
				<image v-if="play" src="../../static/icon/play.png"></image>
				<image v-else src="../../static/icon/shop.png"></image>
			</view>
			<view @click="getsong">
				<text>歌名</text>/
				<text>歌手</text>
			</view>
			<text>❤</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				play: false,
				text: []
			}
		},
		methods: {
			playmusic() {
				if (this.play == false) {
					this.play = true
				} else {
					this.play = false
				}
			},
			// async getsong() {
			// 	let res = await this.$myReq({
			// 		methods: 'GET',
			// 		url: "/?json=true"
			// 	})
			// 	console.log(res)
			// }
			getsong(){
				uni.request({
				    url: '/api/goods/v1/nav', 
				    success: (res) => {
				        console.log(res);
				        this.text = res.data.list;
				    }
				});
			}
		},
		created(){
			this.getsong()
		}
		
	}
</script>

<style>
	.container {
		display: flex;
		justify-content: center;
	}

	.bar {
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: 20px;
		line-height: 20px;
		padding: 20px;
		font-size: 14px;
		font-weight: 600;
		position: fixed;
		bottom: 60px;
		width: 80%;
		height: 12px;
		line-height: 12px;
		background: #E5E5E5;
		border-radius: 20px;
		margin: 30px;
	}

	.bar>view>image {
		width: 22px;
		height: 22px;
	}
</style>
